@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-item{
    font-family: 'Raleway', sans-serif;
    display: flex;
    color: inherit;
    cursor: pointer;
    opacity: 0.8;
    padding: 0 15px;
}

.navbar-item:hover {
    opacity: 1;
}
header {
    background-color: #2c3e50;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding: 0 15px;
}

.navItems {
    display: flex;
}

header h1 {
    text-align: center;
}

header li {
    list-style: none;
}

header a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    opacity: 0.9;
    padding: 0 15px;
}

    header a:hover {
        opacity: 1;
    }

header nav {
    display: flex;
}

header nav {
    display: flex;
}

    header nav li:first-child {
        margin-left: 0;
    }

    header nav li:last-child {
        margin-right: 0;
    }

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input-fields {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    display: flex;
    flex-direction: column;
}
.input {
    margin-top: 6px;
    min-width: 18em;
    height: 30px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
}

form {
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.log-in-button {
    font-size: 21px;
    padding: 5px 20px;
    border: 0;
    background-color: #3968be;
    color: #fff;
    border-radius: 3px;
};
.SelectOptions {
    margin-top: 6px;
    min-width: 18em;
    height: 30px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
}

.meeting-info-container{
    display: flex;
    flex-direction: column;
}

.appointment-card {
	background-color: rgb(255, 255, 255);
	width: 300px;
    margin: 1%;
    border-top: 1px solid;
    border-bottom: 1px solid;
    display: flex;
    flex-direction: row;
    position: relative;
}
.agenda {
    font-size: 16px;
    font-family: 'Raleway', sans-serif;

}
.duration {
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
}

.zoom-username, .join-url, .url{
    font-size: 12px;
    font-family: 'Raleway', sans-serif;
}

.button-container{
    display: flex;
    flex-direction: column;
}

.start-button{
    font-size: 12px;
    padding: 3px 3px;
    border: 0;
    background-color: #3968be;
    color: #fff;
    border-radius: 3px;
}

.delete-button{
    font-size: 12px;
    padding: 3px 3px;
    border: 0;
    background-color: #551616;
    color: #fff;
    border-radius: 3px;

}
