header {
    background-color: #2c3e50;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    padding: 0 15px;
}

.navItems {
    display: flex;
}

header h1 {
    text-align: center;
}

header li {
    list-style: none;
}

header a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    opacity: 0.9;
    padding: 0 15px;
}

    header a:hover {
        opacity: 1;
    }

header nav {
    display: flex;
}

header nav {
    display: flex;
}

    header nav li:first-child {
        margin-left: 0;
    }

    header nav li:last-child {
        margin-right: 0;
    }
